import React from "react";
import { FaRegWindowClose } from "react-icons/fa";
import ReactPlayer from "react-player/youtube";

function VideoPopup({ show, setShow, videoId, setVideoId }) {
  // console.log("active");
  const hidePopup = () => {
    setShow(false);
    setVideoId(null);
  };

  return (
    <div className="top-0 left-0 z-50 fixed w-screen h-screen video-container"  onClick={hidePopup}>
      <div
        className="top-0 absolute bg-black opacity-50 opacity-layer w-full h-full transition-all duration-300 ease-in-out"
       
      ></div>
      <div className="top-0 right-0 md:right-20 xl:right-32 absolute w-full h-full container video-player">
        <div className="relative flex justify-center items-center w-full h-full container">
          {/* <span
            className="top-20 right-2 md:right-40 absolute close-btn"
            onClick={hidePopup}
          >
            <FaRegWindowClose className="text-4xl text-white hover:text-red-500 cursor-pointer" />
          </span> */}
          <div className="relative w-full lg:w-1/2 h-1/2 lg:h-1/2">
 

          <ReactPlayer
            url={`https://www.youtube.com/watch?v=${videoId}`}
            controls
            width="100%"
            height="100%"
            // playing={true}
            />
            </div>
        </div>
      </div>
    </div>
  );
}

export default VideoPopup;
